import React, { useMemo, useState, useCallback } from 'react'
import styled from 'styled-components'
import { Heading, Card, CardBody, Button, Flex } from '@pancakeswap/uikit'

const Block = styled.div`
  margin-bottom: 16px;
`

const StyledImage = styled.img`
  width: 50px;
  height: 50px;
  margin-right: 15px;
`

const Space = styled.div`
  background: linear-gradient(90deg, #FFFFFF 0%, #000000 50%, #FFFFFF 100%);
  opacity: 0.4;
  height: 1px;
  width: 100%;
  margin-bottom: 16px;
`

const Label = styled.div`
  color: ${({ theme }) => theme.colors.text};
  font-size: 14px;
`

const Value = styled.div`
  color: ${({ theme }) => theme.colors.text};
  font-size: 16px;
  font-style: bold;
  font-weight: 700;
  line-height: 36px;
  margin-right: 10px;
`

interface BalanceCardProps {
	image: string
	title: string
	value: string
}

const StatusItem: React.FC<BalanceCardProps> = ({image, title, value}) => {
	return (
    <>
  		<Flex>
  			<StyledImage src={image} alt={image} />
        <Block>
          <Label>{title}</Label>
          <Flex>
            <Value>{value}</Value>
          </Flex>
        </Block>
      </Flex>
      <Space />
    </>
  )
}

export default StatusItem