import React, { useMemo, useState, useCallback } from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { Heading, Card, CardBody, Button, Flex } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import UnlockWalletButton from 'components/UnlockWalletButton'
import Balance from './Balance'
import BalanceCard from './BalanceCard'

const StyledFarmStakingCard = styled(CardBody)`
  background: ${({ theme }) => theme.colors.backgroundAlt};
  border-radius: 10px;
  min-height: 176px;
  margin-bottom: 20px;
`

const Actions = styled.div`
  margin-top: 24px;
`

const FarmedStakingCard = () => {
	const { t } = useTranslation()
	const [pendingTx, setPendingTx] = useState(false)
	const { account } = useWeb3React()

  return (
    <StyledFarmStakingCard>
      <Heading scale="lg" mb="24px">
        {t('Farms & Staking')}
      </Heading>
      <BalanceCard image="images/home/Harvest.svg" title={t('Infinity to Harvest')} value={123 * 13} balance={123} />
      <BalanceCard image="images/home/Wallet.svg" title={t('Infinity in Wallet')} value={123 * 13} balance={123} />
      <Actions>
        {account ? (
          <Button
            id="harvest-all"
            disabled
            onClick={() => { console.log("pooh, harvest button")}}
          >
            {pendingTx
              ? t('Collecting UNO')
              : t(`Harvest all (${111})`)}
              {/* : t(`Harvest all 0`)} */}
              </Button>
        ) : (
          <UnlockWalletButton fullWidth />
        )}
      </Actions>
    </StyledFarmStakingCard>
  )
}

export default FarmedStakingCard