import React, { useMemo, useState, useCallback } from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { Heading, Card, CardBody, Button, Flex } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import { useGetStats } from 'hooks/api'
import { formatLocalisedCompactNumber } from 'utils/formatBalance'
import UnlockWalletButton from 'components/UnlockWalletButton'

const StyledTVLCard = styled(CardBody)`
  background: ${({ theme }) => theme.colors.backgroundAlt};
  border-radius: 10px;
  min-height: 176px;
  margin-bottom: 32px;
`

const Label = styled.div`
  color: ${({ theme }) => theme.colors.text};
  font-weight: 600;
  font-size: 30px;
  line-height: 45px;
`

const StyledImage = styled.img`
  width: 50px;
  height: 50px;
  margin-right: 15px;
`


const TotalValueLocked = () => {
  const data = useGetStats()
	const { t } = useTranslation()
	const [pendingTx, setPendingTx] = useState(false)
	const { account } = useWeb3React()

  const tvlString = data ? formatLocalisedCompactNumber(data.tvl) : '-'

  return (
    <StyledTVLCard>
      <Heading scale="lg" mb="24px">
        {t('Total Value Locked (TVL)')}
      </Heading>
      <Flex>
        <StyledImage src="images/home/TotalValueLocked.svg" alt={t('wallet')} />
        <Label>{t('$%tvl%', { tvl: tvlString })}</Label>        
      </Flex>
    </StyledTVLCard>
  )
}

export default TotalValueLocked