import React, { useMemo, useState, useCallback } from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { Heading, Card, CardBody, Button, Flex } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import UnlockWalletButton from 'components/UnlockWalletButton'
import StatusItem from './StatusItem'

const StyledFarmStakingCard = styled(CardBody)`
  background: ${({ theme }) => theme.colors.backgroundAlt};
  border-radius: 10px;
  min-height: 176px;
  margin-bottom: 32px;
`

const InfinityStatusCard = () => {
	const { t } = useTranslation()
	const [pendingTx, setPendingTx] = useState(false)
	const { account } = useWeb3React()

  return (
    <StyledFarmStakingCard>
      <Heading scale="lg" mb="24px">
        {t('Infinity Status')}
      </Heading>
      <StatusItem image="images/home/MarketCap.svg" title={t('Market Cap')} value="$2,629,489" />
      <StatusItem image="images/home/TotalMinted.svg" title={t('Total Minted')} value="3,774,855" />
      <StatusItem image="images/home/TotalBurn.svg" title={t('Total Burned')} value="321,237" />
      <StatusItem image="images/home/CirculatingSupply.svg" title={t('Circulating Supply')} value="3,453,617" />
      <StatusItem image="images/home/Block.svg" title={t('New Infinity/Block')} value="0.7" />
    </StyledFarmStakingCard>
  )
}

export default InfinityStatusCard